import { Injectable } from '@angular/core';

import { AngularFireAuth } from 'angularfire2/auth';
import { AngularFirestore } from '@angular/fire/firestore';

import * as firebase from 'firebase/app';
import { User, Farm } from './shared/services/user';

import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

@Injectable()
export class AuthService {
  user: Observable<firebase.User>;
  userData: User;

  constructor(private firebaseAuth: AngularFireAuth, private db: AngularFirestore) {
    this.user = firebaseAuth.authState;
  }

  signup(email: string, password: string) {
    this.firebaseAuth
      .auth
      .createUserWithEmailAndPassword(email, password)
      .then(value => {
        console.log('Success!', value);
      })
      .catch(err => {
        console.log('Something went wrong:', err.message);
      });
  }

  login(email: string, password: string) {
    this.firebaseAuth
      .auth
      .signInWithEmailAndPassword(email, password)
      .then(value => {
        console.log('Nice, it worked!');
      })
      .catch(err => {
        console.log('Something went wrong:', err.message);
      });

  }

  logout() {
    this.firebaseAuth
      .auth
      .signOut();
  }


  setFarmData(farmData) {
    const param = farmData.id;

    const tempFarm: Farm = {
      date: new Date(),
      location: farmData.location,
      variety: farmData.variety,
      stage: farmData.stage,
      schedule: farmData.growthSched

    };

    console.log('tempFarm :>> ', tempFarm);
    // tempFarm.schedule.push(farmData.growthSched);
  }

  setUserData(email, password) {

    const tempFarm: Farm = {
      date: new Date(),
      location: 'location',
      variety: 'variety',
      stage: 'stage',
      schedule: 'stages'

    };

    const temp: User =
    {
      uid: email,
      email: email,
      displayName: email,
      photoURL: email,
      emailVerified: true,
      farms: []
    };

    this.userData = temp;
  }


  saveUserData() {
    return this.db.collection('users').add(this.userData).catch(err => {
      console.log('Something went wrong:', err.message);
    });
  }



  getUsers() {
    return this.db.collection("/users").snapshotChanges().pipe(
      map(actions => actions.map(a => {
        const data = a.payload.doc.data() as User;
        const id = a.payload.doc.id;
        return { id, ...data };
      }))
    );
  }


  findUser(email) {
    return this.db.collection('/users', ref => ref.where('email', '>=', email)
      .where('email', '<=', email + '\uf8ff'))
      .snapshotChanges().pipe(
        map(actions => actions.map(a => {
          const data = a.payload.doc.data() as User;
          const id = a.payload.doc.id;
          return { id, ...data };
        }))
      );
  }

}